<template>
    
    <div>
        <Dialog @hide="hide" v-model:visible="visible" modal header="Ek Ücret Güncelle" :style="{ width: '50vw' }" :breakpoints="{ '960px': '75vw', '641px': '95vw' }">
            <form @submit.prevent="update" class="d-flex flex-column h-100">

                <div class="row g-5 mb-5">
                    <div class="col-lg-12">
                        <div class="d-flex align-items-center mb-1">
                            <span class="fw-semibold">İsim</span>
                            <div class="d-flex align-items-center ms-2">
                                <img :src="`/dmn/media/flags/${props.item.language}.svg`" class="me-2" style="width: 14px" />
                                <span class="text-uppercase">{{ props.item.language }}</span>
                            </div>
                        </div>
                        <InputText v-model="form.name" class="w-100" />
                    </div>
                    <div class="col-lg-12">
                        <label class="form-label">Ücret</label>
                        <InputText v-model="form.price" class="w-100" />
                    </div>
                    <div class="col-lg-12">
                        <label class="form-label">Kısa Açıklama</label>
                        <Textarea autoResize rows="1" cols="30" class="w-100" v-model="form.description" />
                    </div>
                    <div class="col-lg-12">
                        <label class="form-label">Kullanılacak Ürün Tipi</label>
                        <Dropdown v-model="form.product_type_id" :options="props.productTypes" optionGroupLabel="name" optionLabel="name" optionValue="id" optionGroupChildren="product_types" placeholder="Lütfen seçiniz" :maxSelectedLabels="5" class="w-100" />
                    </div>
                </div>

                <div class="separator my-10"></div>

                <div class="">
                    <button type="submit" class="btn btn-primary" :class="{ 'opacity-25': form.processing }" :disabled="form.processing">Kaydet</button>
                    <button type="button" class="btn btn-light ms-5" @click="cancel()">Vazgeç</button>
                </div>
                <FormErrors :form="form"/>
            </form>
        </Dialog>
    </div>

</template>

<script setup>

import {ref} from "vue";
import { useForm } from "@inertiajs/vue3";
import Dialog from "primevue/dialog";
import InputText from 'primevue/inputtext';
import Textarea from "primevue/textarea";
import Button from 'primevue/button';
import Dropdown from "primevue/dropdown";

const props = defineProps({
    item:Object,
    productTypes:Object,
    onHide:{
        type:Function,
        default:() => {}
    }
});

const visible = ref(true);

const form = useForm({
    name:props.item.name,
    language:props.item.language,
    description: props.item.description,
    price:props.item.price,
    product_type_id:props.item.product_type_id
});

const cancel = () => {
    visible.value = false;
    form.reset();
};

const update = () => {
    form.put(route('extra-costs.update',props.item),{
        preserveScroll: true,
        onSuccess: (page) => {
            visible.value = false;
        }
    });
}

const hide = () => {
    visible.value = false;
    props.onHide();
}

</script>